          articleName: "在vue项目中 如何定义全局变量 全局函数",
          articleDesc:
            "在项目中，经常有些函数和变量是需要复用，比如说网站服务器地址，从后台拿到的：用户的登录token,用户的地址信息等，这时候就需要设置一波全局变量和全局函数",
          articleImgurl: "./static/public/uploads/1553749000585.jpg",
          classesId: {
            _id: "5c947642779de5468066c488",
            classesName: "前端",
            classesDesc: "关于前端的一些知识点",
            __v: 0
          },
          articleContent:
            '<h2>定义全局变量</h2><h3>原理：</h3><p>设置一个专用的的全局变量模块文件，模块里面定义一些变量初始状态，用export default 暴露出去，在main.js里面使用Vue.prototype挂载到vue实例上面或者在其它地方需要使用时，引入该模块便可。</p><h3>全局变量模块文件：</h3><p>Global.vue文件：</p><pre class="ql-syntax" spellcheck="false">&lt;script&gt;\nconst serverSrc=\'www.baidu.com\';\nconst token=\'12345678\';\nconst hasEnter=false;\nconst userSite="中国钓鱼岛";\n  export default\n  {\n    userSite,//用户地址\n    token,//用户token身份\n    serverSrc,//服务器地址\n    hasEnter,//用户登录状态\n  }\n&lt;/script&gt;复制代码\n</pre><h3>使用方式1：</h3><p><strong>在需要的地方引用进全局变量模块文件</strong>，然后通过文件里面的变量名字获取全局变量参数值。</p><p><strong>在text1.vue组件中使用：</strong></p><pre class="ql-syntax" spellcheck="false">&lt;template&gt;\n    &lt;div&gt;{{ token }}&lt;/div&gt;\n&lt;/template&gt;\n\n&lt;script&gt;\nimport global_ from \'../../components/Global\'//引用模块进来\nexport default {\n name: \'text\',\ndata () {\n    return {\n         token:global_.token,//将全局变量赋值到data里面，也可以直接使用global_.token\n        }\n    }\n}\n&lt;/script&gt;\n&lt;style lang="scss" scoped&gt;\n\n&lt;/style&gt;复制代码\n</pre><h3>使用方式2：</h3><p>在程序入口的main.js文件里面，将上面那个Global.vue文件挂载到Vue.prototype。</p><pre class="ql-syntax" spellcheck="false">    import global_ from \'./components/Global\'//引用文件\n    Vue.prototype.GLOBAL = global_//挂载到Vue实例上面复制代码\n</pre><p>接着在整个项目中不需要再通过引用Global.vue模块文件，直接通过this就可以直接访问Global文件里面定义的全局变量。</p><p>text2.vue：</p><pre class="ql-syntax" spellcheck="false">&lt;template&gt;\n    &lt;div&gt;{{ token }}&lt;/div&gt;\n&lt;/template&gt;\n\n&lt;script&gt;\nexport default {\n name: \'text\',\ndata () {\n    return {\n         token:this.GLOBAL.token,//直接通过this访问全局变量。\n        }\n    }\n}\n&lt;/script&gt;\n&lt;style lang="scss" scoped&gt;\n&lt;/style&gt;复制代码\n</pre><h3>Vuex也可以设置全局变量：</h3><p>通过vuex来存放全局变量，这里东西比较多，也相对复杂一些，有兴趣的小伙伴们，可自行查阅资料，折腾一波、</p><h2>定义全局函数</h2><h3>原理</h3><p>新建一个模块文件，然后在main.js里面通过Vue.prototype将函数挂载到Vue实例上面，通过this.函数名，来运行函数。</p><h3>1. 在main.js里面直接写函数</h3><p>简单的函数可以直接在main.js里面直接写</p><pre class="ql-syntax" spellcheck="false">Vue.prototype.changeData = function (){//changeData是函数名\n  alert(\'执行成功\');\n}复制代码\n</pre><p>组件中调用：</p><pre class="ql-syntax" spellcheck="false">this.changeData();//直接通过this运行函数复制代码\n</pre><h3>2. 写一个模块文件，挂载到main.js上面。</h3><p>base.js文件，文件位置可以放在跟main.js同一级，方便引用</p><pre class="ql-syntax" spellcheck="false">exports.install = function (Vue, options) {\n   Vue.prototype.text1 = function (){//全局函数1\n    alert(\'执行成功1\');\n    };\n    Vue.prototype.text2 = function (){//全局函数2\n    alert(\'执行成功2\');\n    };\n};复制代码\n</pre><p>main.js入口文件：</p><pre class="ql-syntax" spellcheck="false">    import base from \'./base\'//引用\n    Vue.use(base);//将全局函数当做插件来进行注册复制代码\n</pre><p>组件里面调用：</p><pre class="ql-syntax" spellcheck="false">    this.text1();\n    this.text2();\n</pre><p><br></p><p>链接：https://juejin.im/post/59eddbfe6fb9a0450908abb4</p>',
          __v: 0
        },
