            classesDesc: "记录生活的点滴",
            __v: 0
          },
          articleContent:
            '<h2><span style="color: rgb(68, 68, 68);"> </span>\t<strong class="ql-size-14px" style="color: rgb(68, 68, 68);">不积跬步，无以至千里；不积小流，无以至江河。如果不能一小步一小步的走，根本没有办法到达千里之外，如果不能汇聚小河流的河水，那么根本没有办法汇成大江大河。一口吃不成个胖子，一步也登不了天，想要成功，想要成为更优秀的自己，我们除了脚踏实地全身心地努力，别无它法。</strong></h2><h2><strong class="ql-size-14px" style="color: rgb(68, 68, 68);"> 一个人不管靠天靠地靠人脉，如果他自己没有真本事，人生路也走不长。一个人只有越努力，他才会越幸运，他的选择就越多。我觉得人无非三种选择，一是碌碌无为，混混而过；二是随波逐流，坦然接受；三是拼命努力，越来越好。你要选择什么样的生活，最主要还是取决于你自己，因为人家的想法和做法不能代替你去生活。我们不强求人人努力，找寻更美好的自己，但是我希望你来人世一趟，一定要努力发挥自己的聪明才智，把最美好最优秀的一面展现在人们的面前，纵然不十全十美，但是一直在前行的路上，那也足够了。</strong></h2><h2><strong class="ql-size-14px" style="color: rgb(68, 68, 68);">有些人总是不屑一顾地说，心灵鸡汤差不多就行，喝太多会中毒的。其实我想说，我们一生都离不开鸡汤的温热，尤其是你在失落、惆怅、迷茫时，又或是你人生遭遇极大的变故或打击时，陪伴我们渡过困境的恰恰是那些我们认为鄙视的话语。一件事，我们不能十分地肯定，但也不能全面地否定，我们需要审时度势地正反看待，用一双慧眼去识别，坏的置之不理，好的吸收并且践行。</strong></h2><h2><strong class="ql-size-14px" style="color: rgb(68, 68, 68);">汤木著写的《将来的你，一定会感谢现在拼命的自己》，我非常喜欢，句句在理，句句抨击着我们的内心，我也年轻过，也有过十八二十的青春，那时总是不在意人生到底会走向哪里，因为年轻无敌，后来随着年龄的增长，恋爱结婚工作，这一路走得格外辛苦，但是今天纵然前面有再多的艰难与困苦，我不会再有年轻的冲动，也能相对坦然地面对。人只有经历过风雨，才能见彩虹，没有人能随随便便成功。就如汤木所说，时间是最公平的资源，你把时间用在哪里，它就会在哪里结出果实。今天你所做的每一点看似平凡的努力，都是在为你的未来积累能量。不管任何时候，我们都要多读书，对自己有一个清醒的认识，你的努力不一定能立马见效，但是相信你总有一天会看到一个越来越辽阔的未来。</strong></h2><h2><strong class="ql-size-14px" style="color: rgb(68, 68, 68);"> </strong>\t<strong class="ql-size-14px" style="color: rgb(68, 68, 68);">我们只是沙漠中的一粒小小的沙子，芸芸众生中最普通的一个，但是我相信一个人越努力，他的选择就会越多；不要寻找任何借口让你自暴自弃，唯有义务反顾，才能勇往直前；人生要有主见，要有目标，才能不迷茫，心中有了方向，才不会一路跌跌撞撞；吃亏是福，不必计较眼前小利，胸怀宽广路也广；播下一种好的习惯，收获一种好的命运；向优秀的人学习，和优秀的人做朋友，建立良好的人际关系；学会克制情绪，不要让“忍不住，图一时之快”害你一生……我相信命运多一份磨砺，就多一份强大。今时今日拼一把，明天你一定会感谢今天的自己。</strong></h2><h2><strong class="ql-size-14px" style="color: rgb(68, 68, 68);">我相信今天你做好了自己，明天一定会更美好！</strong></h2><h2><strong class="ql-size-14px" style="color: rgb(68, 68, 68);">《将来的你，一定会感谢现在拼命的自己（全新升级版）》读后感(二)：要么去死 要么精彩的活着</strong></h2><h1><span style="color: rgb(68, 68, 68);"> </span>\t<span class="ql-size-14px" style="color: rgb(68, 68, 68);">要么去死 要么精彩的活着</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">蔡永康在 《给残酷社会的善意短信》 中有一段话：15岁觉得游泳难，放弃游泳，到18岁遇到一个你喜欢的人约你去游泳，你只会说：“我不会耶” 18岁觉得英文难，放弃英文，28岁出现一个很棒但要会英文的工作，你只好说：“我不会耶” 人生前期越嫌烦，越懒的学，后期就越可能错过让你动心的人和事，错过新风景。 所以现在拼命努力将来你一定会感谢现在的你。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">这便是汤木先生所著《将来的你，一定会感谢现在拼命的自己》的出处，仿佛蔡永康老师的这段话更能体现汤木先生的意思啊，汤木先生只是把意思扩展一下了。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">其实我生平最不喜欢的就是这种无痛无痒的书了，动不动就无病呻吟。什么心灵砒霜啊，激励人生啊，对不住我不需要，我的人生自然有我自己的走法，我的未来原意为我现在的所作所为承担，你的鸡汤我嫌烫，不适合我。小清新的书在我的眼中无非就是无病呻吟，我的青春、我的大学、我的人生······这在我的眼中都是扯淡。或许你是过来人，但是请不要以一种高高在上的姿态来指点我的江山。每个人都有每个人的活法，请不要去用你的法则去干涉我的朝政，你不配。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">但是，这本书带给我的感觉就不一样。看书名你就能够感受的到，这不是一本说教的书籍。或许正是我赞成了蔡永康的那句话，才让我有机会接触了这本书籍吧。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">要么去死！要么精彩地活着！这便是我第一句接受的话，是我对这本书转变的开始。汤木可以说是在豆瓣上拥有超高人气的写手，（感觉我发到豆瓣上如果被看到会不会被干死）而这句话便是他写给世界的狠话！可谓是激励了千万在生活中默默奋斗着的年轻人啊！出版社都美其名曰：是冯仑、张德芬、毕淑敏、柴静等人希望你早日能够看到的一本正能量书！（这话说得有点过了吧，可能有理有据，不过人家可能只是客套一下吧，总而言之这是出版社的事，不适合吐槽）。这已经不是他的第一本书了，曾经出版过《我若不勇敢，谁替我坚强》《你受的苦，总有一天会照亮你未来的路》等图书（看样子是出不了心灵鸡汤这个坑了，而且书名取得就不能简单点，正所谓言简意赅吗，不吐槽），如此来看汤木先生是一个希望可以用文字诚实表达自己的想法和心情的人。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">真的是很荣幸我读到的是再出版的一本书，里面在原来的基础上又加了两章《你的努力，时间都会帮你兑现》和《每场漫不经心的成功，其实都是深思熟虑》。全书100多篇真诚之作，十万多字的肺腑之言，可以算得上经验之谈。内容也是激励千万读者，故事好像都是网上那些点赞无数的心灵小鸡汤。让内心浮躁、内心迷茫的人坚定了内心、看清了方向，引导我们去方向更辽阔的天空，为将来的自己赢得一片美好未来。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);"> 借用朋友对这本书评价的一句话：将来的你，一定会感谢现在拼命努力的自己，每一个你讨厌的现在，都有一个不够努力的曾经，你尽力了，才有资格说自己的运气不好。成长的背后，必定是心酸的付出。</span></h1><h1><strong class="ql-size-14px" style="color: rgb(68, 68, 68);">《将来的你，一定会感谢现在拼命的自己（全新升级版）》读后感(三)：拼一拼才知道</strong></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);"> </span>\t<span class="ql-size-14px" style="color: rgb(68, 68, 68);">若在6、8年前有这么一本书读，应该会特别喜欢。可能那时候还年少，比起现在的心更容易感染。书中开头写的“青春不会无限延长，它经不起辜负”，青春转眼即逝，一去不复返，那时候也最容易造成遗憾和后悔。 大杂烩鸡汤，料很足；料还是那些料，没喝过的人觉得好喝，喝过的人不以为然。我不喜读励志的书，因为那是在赤裸裸地验证我的无动于衷。 一个人静静地看着，想从中寻找些什么，我发现汤中的很多料我曾经在别处吃过，却无从得知。我该高兴吗？毕竟作者精心加的料，熬制的汤，我却知道了大半的配方。 一次感冒，吃了感冒药治好了我的病，后来的感冒再吃这种感冒药就不起作用了，这免疫是自然的。错的不是药，感冒也不可避免，最好的做法是靠自身平时的调节。所以，我还是认真地读完了。满满的正能量，那些熟悉的故事，我还能感觉到第一次遇见时候胸口的热度。在热度退减之后，无形之中还能融入我的身体，变成养分，这才是我想要的。 “将来的你一定会感谢现在拼命的自己”。这充满希望又自信的话，这包含艰难且自我感动的话。 那些 “不平凡的人”，你羡慕，你可望而不可即，命运是什么？理想又是什么？不是电影不是小说，早有导演和作者预先开展。它们应该紧握在自己的手上和藏在自己的心里，由你支配。路途中有太多意外，无论发生了什么，请不要忘了最初的自己。 长大后的你，不会像婴孩般有人牵着你一步一步迈开脚步。你想要的，想做的，遇到的，失去的，快乐的，难过的……都由自己去判断去抉择。人间炼狱，过程痛苦，承受下来的结果将如愿以偿。想要的东西都不容易得到，不容易得到的东西却容易放弃，容易得到的东西却不在意。应竭尽全力的时候你没有，在唾手可得的时候你也没有。或许是你不敢竭尽全力，担心害怕，没有如愿以偿然后就放弃了。没错，有时候的自己就是这样，讨厌吧。 现在的我也应该感谢过去的自己，那个讨厌的自己，将来不要再那样的讨厌了。 剖下外面的封面，是红色的书皮，前两章是彩印，书中插图静美。送的笔记本纸张很厚，挺喜欢的。</span></h1>',
          __v: 0
        },
        {
          tags: ["vue", "面试"],
          clickTimes: 0,
          createTime: "2019-03-26T03:02:20.346Z",
