          articleContent:
            '<h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">一、对于MVVM的理解？ </span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">MVVM 是 Model-View-ViewModel 的缩写。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Model代表数据模型，也可以在Model中定义数据修改和操作的业务逻辑。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">View 代表UI 组件，它负责将数据模型转化成UI 展现出来。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">ViewModel 监听模型数据的改变和控制视图行为、处理用户交互，简单理解就是一个同步View 和 Model的对象，连接Model和View。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">在MVVM架构下，View 和 Model 之间并没有直接的联系，而是通过ViewModel进行交互，Model 和 ViewModel 之间的交互是双向的， 因此View 数据的变化会同步到Model中，而Model 数据的变化也会立即反应到View 上。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">ViewModel 通过双向数据绑定把 View 层和 Model 层连接了起来，而View 和 Model 之间的同步工作完全是自动的，无需人为干涉，因此开发者只需关注业务逻辑，不需要手动操作DOM, 不需要关注数据状态的同步问题，复杂的数据状态维护完全由 MVVM 来统一管理。</span></h1><p><br></p><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">二、Vue的生命周期</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">beforeCreate（创建前） 在数据观测和初始化事件还未开始</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">created（创建后） 完成数据观测，属性和方法的运算，初始化事件，$el属性还没有显示出来</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">beforeMount（载入前） 在挂载开始之前被调用，相关的render函数首次被调用。实例已完成以下的配置：编译模板，把data里面的数据和模板生成html。注意此时还没有挂载html到页面上。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">mounted（载入后） 在el 被新创建的 vm.$el 替换，并挂载到实例上去之后调用。实例已完成以下的配置：用上面编译好的html内容替换el属性指向的DOM对象。完成模板中的html渲染到html页面中。此过程中进行ajax交互。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">beforeUpdate（更新前） 在数据更新之前调用，发生在虚拟DOM重新渲染和打补丁之前。可以在该钩子中进一步地更改状态，不会触发附加的重渲染过程。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">updated（更新后） 在由于数据更改导致的虚拟DOM重新渲染和打补丁之后调用。调用时，组件DOM已经更新，所以可以执行依赖于DOM的操作。然而在大多数情况下，应该避免在此期间更改状态，因为这可能会导致更新无限循环。该钩子在服务器端渲染期间不被调用。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">beforeDestroy（销毁前） 在实例销毁之前调用。实例仍然完全可用。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">destroyed（销毁后） 在实例销毁之后调用。调用后，所有的事件监听器会被移除，所有的子实例也会被销毁。该钩子在服务器端渲染期间不被调用。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">1.什么是vue生命周期？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答： Vue 实例从创建到销毁的过程，就是生命周期。从开始创建、初始化数据、编译模板、挂载Dom→渲染、更新→渲染、销毁等一系列过程，称之为 Vue 的生命周期。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">2.vue生命周期的作用是什么？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：它的生命周期中有多个事件钩子，让我们在控制整个Vue实例的过程时更容易形成好的逻辑。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">3.vue生命周期总共有几个阶段？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：它可以总共分为8个阶段：创建前/后, 载入前/后,更新前/后,销毁前/销毁后。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">4.第一次页面加载会触发哪几个钩子？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：会触发 下面这几个beforeCreate, created, beforeMount, mounted 。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">5.DOM 渲染在 哪个周期中就已经完成？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：DOM 渲染在 mounted 中就已经完成了。</span></h1><p><br></p><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">三、 Vue实现数据双向绑定的原理：Object.defineProperty()</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">vue实现数据双向绑定主要是：采用数据劫持结合发布者-订阅者模式的方式，通过Object.defineProperty()来劫持各个属性的setter，getter，在数据变动时发布消息给订阅者，触发相应监听回调。当把一个普通 Javascript 对象传给 Vue 实例来作为它的 data 选项时，Vue 将遍历它的属性，用 Object.defineProperty 将它们转为 getter/setter。用户看不到 getter/setter，但是在内部它们让 Vue 追踪依赖，在属性被访问和修改时通知变化。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">vue的数据双向绑定 将MVVM作为数据绑定的入口，整合Observer，Compile和Watcher三者，通过Observer来监听自己的model的数据变化，通过Compile来解析编译模板指令( vue中是用来解析 { { } } )，最终利用watcher搭起observer和Compile之间的通信桥梁，达到数据变化 —&gt;视图更新；视图交互变化（input）—&gt;数据model变更双向绑定效果。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">js实现简单的双向绑定</span></h1><pre class="ql-syntax" spellcheck="false">&lt;body&gt;\n    &lt;div id="app"&gt;\n    &lt;input type="text" id="txt"&gt;\n    &lt;p id="show"&gt;&lt;/p&gt;\n&lt;/div&gt;\n&lt;/body&gt;\n&lt;script type="text/javascript"&gt;\n    var obj = {}\n    Object.defineProperty(obj, \'txt\', {\n        get: function () {\n            return obj\n        },\n        set: function (newValue) {\n            document.getElementById(\'txt\').value = newValue\n            document.getElementById(\'show\').innerHTML = newValue\n        }\n    })\n    document.addEventListener(\'keyup\', function (e) {\n        obj.txt = e.target.value\n    })\n&lt;/script&gt;\n</pre><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">四、Vue组件间的参数传递</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">1.父组件与子组件传值</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">父组件传给子组件：子组件通过props方法接受数据;</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">子组件传给父组件：$emit方法传递参数</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">2.非父子组件间的数据传递，兄弟组件传值</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">eventBus，就是创建一个事件中心，相当于中转站，可以用它来传递事件和接收事件。项目比较小时，用这个比较合适。（虽然也有不少人推荐直接用VUEX，具体来说看需求咯。技术只是手段，目的达到才是王道。）</span></h1><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">五、Vue的路由实现：hash模式 和 history模式</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">hash模式：在浏览器中符号“#”，#以及#后面的字符称之为hash，用window.location.hash读取；</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">特点：hash虽然在URL中，但不被包括在HTTP请求中；用来指导浏览器动作，对服务端安全无用，hash不会重加载页面。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">history模式：history采用HTML5的新特性；且提供了两个新方法：pushState（），replaceState（）可以对浏览器历史记录栈进行修改，以及popState事件的监听到状态变更。</span></h1><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">六、Vue与Angular以及React的区别？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">（版本在不断更新，以下的区别有可能不是很正确。我工作中只用到vue，对angular和react不怎么熟）</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">1.与AngularJS的区别</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">相同点：</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">都支持指令：内置指令和自定义指令；都支持过滤器：内置过滤器和自定义过滤器；都支持双向数据绑定；都不支持低端浏览器。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">不同点：</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">AngularJS的学习成本高，比如增加了Dependency Injection特性，而Vue.js本身提供的API都比较简单、直观；在性能上，AngularJS依赖对数据做脏检查，所以Watcher越多越慢；Vue.js使用基于依赖追踪的观察并且使用异步队列更新，所有的数据都是独立触发的。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">2.与React的区别</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">相同点：</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">React采用特殊的JSX语法，Vue.js在组件开发中也推崇编写.vue特殊文件格式，对文件内容都有一些约定，两者都需要编译后使用；中心思想相同：一切都是组件，组件实例之间可以嵌套；都提供合理的钩子函数，可以让开发者定制化地去处理需求；都不内置列数AJAX，Route等功能到核心包，而是以插件的方式加载；在组件开发中都支持mixins的特性。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">不同点：</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">React采用的Virtual DOM会对渲染出来的结果做脏检查；Vue.js在模板中提供了指令，过滤器等，可以非常方便，快捷地操作Virtual DOM。</span></h1><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">七、vue路由的钩子函数</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">首页可以控制导航跳转，beforeEach，afterEach等，一般用于页面title的修改。一些需要登录才能调整页面的重定向功能。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">beforeEach主要有3个参数to，from，next：</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">to：route即将进入的目标路由对象，</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">from：route当前导航正要离开的路由</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">next：function一定要调用该方法resolve这个钩子。执行效果依赖next方法的调用参数。可以控制网页的跳转。</span></h1><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">八、vuex是什么？怎么使用？哪种功能场景使用它？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">只用来读取的状态集中放在store中； 改变状态的方式是提交mutations，这是个同步的事物； 异步逻辑应该封装在action中。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">在main.js引入store，注入。新建了一个目录store，….. export 。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">场景有：单页应用中，组件之间的状态、音乐播放、登录状态、加入购物车</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">state</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Vuex 使用单一状态树,即每个应用将仅仅包含一个store 实例，但单一状态树和模块化并不冲突。存放的数据状态，不可以直接修改里面的数据。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">mutations</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">mutations定义的方法动态修改Vuex 的 store 中的状态或数据。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">getters</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">类似vue的计算属性，主要用来过滤一些数据。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">action</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">actions可以理解为通过将mutations里面处里数据的方法变成可异步的处理数据的方法，简单的说就是异步操作数据。view 层通过 store.dispath 来分发 action。</span></h1><pre class="ql-syntax" spellcheck="false">const store = new Vuex.Store({ //store实例\n      state: {\n         count: 0\n             },\n      mutations: {                \n         increment (state) {\n          state.count++\n         }\n          },\n      actions: { \n         increment (context) {\n          context.commit(\'increment\')\n   }\n }\n})\n</pre><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">modules</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">项目特别复杂的时候，可以让每一个模块拥有自己的state、mutation、action、getters,使得结构非常清晰，方便管理。</span></h1><pre class="ql-syntax" spellcheck="false">const moduleA = {\n  state: { ... },\n  mutations: { ... },\n  actions: { ... },\n  getters: { ... }\n }\nconst moduleB = {\n  state: { ... },\n  mutations: { ... },\n  actions: { ... }\n }\n \nconst store = new Vuex.Store({\n  modules: {\n    a: moduleA,\n    b: moduleB\n})\n</pre><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">九、vue-cli如何新增自定义指令？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">1.创建局部指令</span></h1><pre class="ql-syntax" spellcheck="false">var app = new Vue({\n    el: \'#app\',\n    data: {    \n    },\n    // 创建指令(可以多个)\n    directives: {\n        // 指令名称\n        dir1: {\n            inserted(el) {\n                // 指令中第一个参数是当前使用指令的DOM\n                console.log(el);\n                console.log(arguments);\n                // 对DOM进行操作\n                el.style.width = \'200px\';\n                el.style.height = \'200px\';\n                el.style.background = \'#000\';\n            }\n        }\n    }\n})\n</pre><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">2.全局指令</span></h1><pre class="ql-syntax" spellcheck="false">Vue.directive(\'dir2\', {\n    inserted(el) {\n        console.log(el);\n    }\n})\n</pre><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">3.指令的使用</span></h1><pre class="ql-syntax" spellcheck="false">&lt;div id="app"&gt;\n    &lt;div v-dir1&gt;&lt;/div&gt;\n    &lt;div v-dir2&gt;&lt;/div&gt;\n&lt;/div&gt;\n</pre><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">十、vue如何自定义一个过滤器？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">html代码：</span></h1><pre class="ql-syntax" spellcheck="false">&lt;div id="app"&gt;\n     &lt;input type="text" v-model="msg" /&gt;\n     {{msg| capitalize }}\n&lt;/div&gt;\n</pre><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">JS代码：</span></h1><pre class="ql-syntax" spellcheck="false">var vm=new Vue({\n    el:"#app",\n    data:{\n        msg:\'\'\n    },\n    filters: {\n      capitalize: function (value) {\n        if (!value) return \'\'\n        value = value.toString()\n        return value.charAt(0).toUpperCase() + value.slice(1)\n      }\n    }\n})\n</pre><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">全局定义过滤器</span></h1><pre class="ql-syntax" spellcheck="false">Vue.filter(\'capitalize\', function (value) {\n  if (!value) return \'\'\n  value = value.toString()\n  return value.charAt(0).toUpperCase() + value.slice(1)\n})\n</pre><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">过滤器接收表达式的值 (msg) 作为第一个参数。capitalize 过滤器将会收到 msg的值作为第一个参数。</span></h1><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">十一、对keep-alive 的了解？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">keep-alive是 Vue 内置的一个组件，可以使被包含的组件保留状态，或避免重新渲染。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">在vue 2.1.0 版本之后，keep-alive新加入了两个属性: include(包含的组件缓存) 与 exclude(排除的组件不缓存，优先级大于include) 。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">使用方法</span></h1><pre class="ql-syntax" spellcheck="false">&lt;keep-alive include=\'include_components\' exclude=\'exclude_components\'&gt;\n  &lt;component&gt;\n    &lt;!-- 该组件是否缓存取决于include和exclude属性 --&gt;\n  &lt;/component&gt;\n&lt;/keep-alive&gt;\n</pre><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">参数解释</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">include – 字符串或正则表达式，只有名称匹配的组件会被缓存</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">exclude – 字符串或正则表达式，任何名称匹配的组件都不会被缓存</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">include 和 exclude 的属性允许组件有条件地缓存。二者都可以用“，”分隔字符串、正则表达式、数组。当使用正则或者是数组时，要记得使用v-bind 。</span></h1><pre class="ql-syntax" spellcheck="false">使用示例\n&lt;!-- 逗号分隔字符串，只有组件a与b被缓存。 --&gt;\n&lt;keep-alive include="a,b"&gt;\n  &lt;component&gt;&lt;/component&gt;\n&lt;/keep-alive&gt;\n \n&lt;!-- 正则表达式 (需要使用 v-bind，符合匹配规则的都会被缓存) --&gt;\n&lt;keep-alive :include="/a|b/"&gt;\n  &lt;component&gt;&lt;/component&gt;\n&lt;/keep-alive&gt;\n \n&lt;!-- Array (需要使用 v-bind，被包含的都会被缓存) --&gt;\n&lt;keep-alive :include="[\'a\', \'b\']"&gt;\n  &lt;component&gt;&lt;/component&gt;\n&lt;/keep-alive&gt;\n</pre><h1><br></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">十二、一句话就能回答的面试题</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">css只在当前组件起作用</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：在style标签中写入scoped即可 例如：</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">v-if 和 v-show 区别</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：v-if按照条件是否渲染，v-show是display的block或none；</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">$route和$router的区别</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：$route是“路由信息对象”，包括path，params，hash，query，fullPath，matched，name等路由信息参数。而$router是“路由实例”对象包括了路由的跳转方法，钩子函数等。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">vue.js的两个核心是什么？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：数据驱动、组件系统</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">vue几种常用的指令</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：v-for 、 v-if 、v-bind、v-on、v-show、v-else</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">vue常用的修饰符？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：.prevent: 提交事件不再重载页面；.stop: 阻止单击事件冒泡；.self: 当事件发生在该元素本身而不是子元素的时候会触发；.capture: 事件侦听，事件发生的时候会调用</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">v-on 可以绑定多个方法吗？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：可以</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">vue中 key 值的作用？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：当 Vue.js 用 v-for 正在更新已渲染过的元素列表时，它默认用“就地复用”策略。如果数据项的顺序被改变，Vue 将不会移动 DOM 元素来匹配数据项的顺序， 而是简单复用此处每个元素，并且确保它在特定索引下显示已被渲染过的每个元素。key的作用主要是为了高效的更新虚拟DOM。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">什么是vue的计算属性？</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：在模板中放入太多的逻辑会让模板过重且难以维护，在需要对数据进行复杂处理，且可能多次使用的情况下，尽量采取计算属性的方式。好处：①使得数据处理结构清晰；②依赖于数据，数据更新，处理结果自动更新；③计算属性内部this指向vm实例；④在template调用时，直接写计算属性名即可；⑤常用的是getter方法，获取数据，也可以使用set方法改变数据；⑥相较于methods，不管依赖的数据变不变，methods都会重新计算，但是依赖数据不变的时候computed从缓存中获取，不会重新计算。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">vue等单页面应用及其优缺点</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">答：优点：Vue 的目标是通过尽可能简单的 API 实现响应的数据绑定和组合的视图组件，核心是一个响应的数据绑定系统。MVVM、数据驱动、组件化、轻量、简洁、高效、快速、模块友好。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">缺点：不支持低版本的浏览器，最低只支持到IE9；不利于SEO的优化（如果要支持SEO，建议通过服务端来进行渲染组件）；第一次加载首页耗时相对长一些；不可以使用浏览器的导航按钮需要自行实现前进、后退。</span></h1><h1><span class="ql-size-14px" style="color: rgb(68, 68, 68);">PS：缺少的案例代码，这几天再补上去。有些地方可能描述的不够清楚，如果有歧义，可能是我理解错了。</span></h1>',
