        },
        {
          tags: ["前端优化"],
          clickTimes: 0,
          createTime: "2019-03-26T03:02:20.346Z",
          updateTime: "2019-03-28T04:57:22.621Z",
          _id: "5c99a1da7d28793bfcacdf99",
          articleName: "35条前端优化建议",
          articleDesc:
            "可以分为 7大类 35条。包括内容 、服务器 、 CSS 、 JavaScript 、Cookie 、图片 、移动应用 ，七部分",
          articleImgurl: "./static/public/uploads/1553749035131.png",
          classesId: {
            _id: "5c947642779de5468066c488",
            classesName: "前端",
            classesDesc: "关于前端的一些知识点",
            __v: 0
          },
          articleContent:
            '<pre class="ql-syntax" spellcheck="false">一、内容部分\n</pre><blockquote>尽量减少 HTTP请求</blockquote><blockquote>减少 DNS查找</blockquote><blockquote>避免跳转</blockquote><blockquote>缓存 Ajxa</blockquote><blockquote>推迟加载</blockquote><blockquote>提前加载</blockquote><blockquote>减少 DOM元素数量</blockquote><blockquote>用域名划分页面内容</blockquote><blockquote>使 frame数量最少</blockquote><blockquote>避免 404错误</blockquote><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">1、尽量减少 HTTP请求次数</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;终端用户响应的时间中，有 80%用于下载各项内容。这部分时间包括下载页面中的图像、样式表、脚本、 Flash等。通过减少页面中的元素可以减少 HTTP请求的次数。这是提高网页速度的关键步骤。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;减少页面组件的方法其实就是简化页面设计。那么有没有一种方法既能保持页面内容的丰富性又能达到加快响应时间的目的呢？这里有几条减少HTTP请求次数同时又可能保持页面内容丰富的技术。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;合并文件是通过把所有的脚本放到一个文件中来减少 HTTP请求的方法，如可以简单地把所有的 CSS文件都放入一个样式表中。当脚本或者样式表在不同页面中使用时需要做不同的修 改，这可能会相对麻烦点，但即便如此也要把这个方法作为改善页面性能的重要一步。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;CSS Sprites是减少图像请求的有效方法。把所有的背景图像都放到一个图片文件中，然后通过 CSS的 background-image和 background-position属性来显示图片的不同部分</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;图片地图是把多张图片整合到一张图片中。虽然文件的总体大小不会改变，但是可 以减少 HTTP请求次数。图片地图只有在图片的所有组成部分在页面中是紧挨在一起的时候才能 使用，如导航栏。确定图片的坐标和可能会比较繁琐且容易出错，同时使用图片地图导航也不具有可读性，因此不推荐这种方法；</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;内联图像是使用 data:URL scheme的方法把图像数据加载页面中。这可能会增加页面的大 小。把内联图像放到样式表（可缓存）中可以减少 HTTP请求同时又避免增加页面文件的大小。但是内联图像现在还没有得到主流浏览器的 支持。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;减少页面的 HTTP请求次数是你首先要做的一步。这是改进首次访问用户等待时间的最重要的方法。 如同 Tenni Theurer的他的博客 Browser Cahe Usage - Exposed!中所说， HTTP请求在无缓存情况下占去了 40%到 60%的响应时间。让那些初次访问你网站的人获得更加快速的体验吧！</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">2、减少 DNS查找次数</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;域名系统（ DNS）提供了域名和 IP的对应关系，就像电话本中人名和他们的电话号码的关系一样。当你在浏览器地址栏中 输入 www.yahoo.com 时， DNS解析服务器就会返回这个域名对应的 IP地址。 DNS解析的过程同样也是需要时间的。一般情况下返回给定域名对应的 IP地址会花费 20到 120毫秒的时间。而且在这个过程中浏览器什么都不会做直到 DNS查找完毕。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;缓存 DNS查找可以改善页面性能。这种缓存需要一个特定的缓存服务器，这种服务器一般属于用户的 ISP提供商或者本地局域网控制，但是它同样会在用户使用的计算机上产生缓存。 DNS信息会保留在操作系统的 DNS缓存中（微软 Windows系统中 DNS Client Service）。大多数浏览器有独立于操作系统以外的自己的缓存。由于浏览器有自己的缓存记录，因此在一次请求中它不会受到操作系统的影响。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Internet Explorer 默认情况下对 DNS查找记录的缓存时间为 30分钟，它在注册表中的键值为 DnsCacheTimeout。 Firefox对 DNS的查找记录缓存时间为 1分钟，它在配置文件中的选项为 network.dnsCacheExpiration（ Fasterfox把这个选项改为了 1小时）。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;当客户端中的 DNS缓存都为空时（浏览器和操作系统都为空）， DNS查找的次数和页面中主机名的数量相同。这其中包括页面中 URL、图片、脚本文件、样式表、Flash对象等包含的主机名。减少主机名的数量可以减少DNS查找次数。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;减少主机名的数量还可以减少页面中并行下载的数量。减少 DNS查找次数可以节省响应时间，但是减少并行下载却会增加响应时间。我的指导原则是 把这些页面中的内容分割成至少两部分但不超过四部分。这种结果就是在减少 DNS查找次数和保持较高程度并行下载两者之间的权衡了。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">3、避免跳转</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">跳转是使用 301和 302代码实现的。下面是一个响应代码为 301的 HTTP头：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">HTTP/1.1 301 Moved Permanently</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Location:&nbsp;http://example.com/newuri</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Content-Type: text/html</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;浏览器会把用户指向到 Location中指定的 URL。头文件中的所有信息在一次跳转中都是必需的，内容部分可以为空。不管他们的名 称， 301和 302响应都不会被缓存除非增加一个额外的头选项，如 Expires或者 Cache-Control来指定它缓存。&nbsp;元素的刷新标签和JavaScript也可以实现 URL的跳转，但是如果你必须要跳转的时候，最好的方法就是使用标准的 3XXHTTP状态代码，这主要是为了确保“后退”按钮可以正确地使用。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;但是要记住跳转会降低用户体验。在用户和 HTML文档中间增加一个跳转，会拖延页面中所有元素的显示，因为在 HTML文件被加载前任何文件（图像、 Flash等）都不会被下载。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;有一种经常被网页开发者忽略却往往十分浪费响应时间的跳转现象。 这种现象发生在当 URL本该有斜杠（ /）却被忽略掉时。例如，当我们要访问&nbsp;http://astrology.yahoo.com/astrology时，实际上返回的是一个包含 301代码的跳转，它指向的是&nbsp;http://astrology.yahoo.com/astrology/&nbsp;（注意末尾的斜杠）。在 Apache服务器中可以使用 Alias 或者 mod_rewrite或者 the DirectorySlash来避免。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;连接新网站和旧网站是跳转功能经常被用到的另一种情况。这种情况 下往往要连接网站的不同内容然后根据用户的不同类型（如浏览器类型、用户账号所属类型）来进行跳转。使用跳转来实现两个网站的切换十分简单，需要的代码量 也不多。尽管使用这种方法对于开发者来说可以降低复杂程度，但是它同样降低用户体验。一个可替代方法就是如果两者在同一台服务器上时使用 Alias和 mod_rewrite和实现。如果是因为域名的不同而采用跳转，那么可以通过使用 Alias或者 mod_rewirte建立 CNAME（保存一个域名和另外一个域名之间关系的DNS记录）来替代。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">4、可缓存的 AJAX</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Ajax 经常被提及的一个好处就是由于其从后台服务器传输信息 的异步性而为用户带来的反馈的即时性。但是，使用 Ajax并不能保证用户不会在等待异步的 JavaScript和 XML响应上花费时间。在很多应用中，用户是否需要等待响应取决于 Ajax如何来使用。例如，在一个基于 Web的 Email客户端中，用户必须等待 Ajax返回符合他们条件的邮件查询结果。记住一点，“异步”并不异味着“即时”，这 很重要。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;为了提高性能，优化 Ajax响应是很重要的。提高 Ajxa性能的措施中最重要的方法就是使响应具有可缓存性，具体的讨论可以查看 Add an Expires or a Cache-Control Header。 其它的几条规则也同样适用于Ajax：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">1. Gizp 压缩文件</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">2. 减少 DNS查找次数</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">3. 精简 JavaScript</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">4. 避免跳转</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">5. 配置 ETags</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;让我们来看一个例子：一个 Web2.0的 Email客户端会使用 Ajax来自动完成对用户地址薄的下载。如果用户在上次使用过 Email web应用程序后没有对地址薄作任何的修改，而且 Ajax响应通过 Expire或者 Cacke-Control头来实现缓存，那么就可以直接从上一次的缓存中读取地址薄 了。必须告知浏览器是使用缓存中的地址薄还是发送一个新的请求。这可以通过为读取地址薄的 Ajax URL增加一个含有上次编辑时间的时间戳来实现，例如， &amp;t=11900241612等。如果地址薄在上次下载后没有被编辑过，时间 戳就不变，则从浏览器的缓存中加载从而减少了一次 HTTP请求过程。如果用户修改过地址薄，时间戳就会用来确定新的 URL和缓存响应并不匹配，浏览器就会重要请求更新地址薄。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;即使你的 Ajxa响应是动态生成的，哪怕它只适用于一个用户，那么它也应该被缓存起来。这样做 可以使你的 Web2.0应用程序更加快捷。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">5、推迟加载内容</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;你可以仔细看一下你的网页，问问自己“哪些内容是页面呈现时 所必需首先加载的？哪些内容和结构可以稍后再加载？</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;把整个过程按照 onload事件分隔成两部分， JavaScript是一个理想的选择。例如，如果你有用于实现拖放和动画的 JavaScript，那么它就以等待稍后加载，因为页面上的拖放元素是在初始化呈现 之后才发生的。其它的例如隐藏部分的内容（用户操作之后才显现的内容）和处于折叠部分的图像也可以推迟加载</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;工具可以节省你的工作量： YUI Image Loader可以帮你推迟加载折叠部分的图片， YUI Get utility是包含 JS和 CSS的便捷方法。比如你可以打开 Firebug的 Net选项卡看一下 Yahoo的首页。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;当性能目标和其它网站开发实践一致时就会相得益彰。这种情况 下，通过程序提高网站性能的方法告诉我们，在支持 JavaScript的情况下，可以先去除用户体验，不过这要保证你的网站在没有 JavaScript也可以正常运行。在确定页面运行正常后，再加载脚本来实现如拖放和动画等更加花哨的效果。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">6、预加载</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;预加载和后加载看起来似乎恰恰相反，但实际上预加载是为了实 现另外一种目标。预加载是在浏览器空闲时请求将来可能会用到的页面内容（如图像、样式表和脚本）。使用这种方法，当用户要访问下一个页面时，页面中的内容 大部分已经加载到缓存中了，因此可以大大改善访问速度。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">下面提供了几种预加载方法：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">无条件加载：触发 onload事件时，直接加载额外的页面内容。以 Google.com为例，你可以看一下它的spirit image图像是怎样在 onload中加载的。这个 spirit image图像在 google.com主页中是不需要的，但是却可以在搜索结果页面中用到它。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">有条件加载：根据用户的操作来有根据地判断用户下面可能去往的页面并相应的预 加载页面内容。在search.yahoo.com中你可以看到如何在你输入内容时加载额外的页面内容。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">有预期的加载：载入重新设计过的页面时使用预加载。这种情况经常出现在页面经过重新设计后用户抱怨“新的页面看起来很酷，但是却比以前慢”。问题可能出在 用户对于你的旧站点建立了完整的缓存，而对于新站点却没有任何缓存内容。因此你可以在访问新站之前就加载一部内容来避免这种结果的出现。在你的旧站中利用 浏览器的空余时间加载新站中用到的图像的和脚本来提高访问速度。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">7、减少 DOM元素数量</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;一个复杂的页面意味着需要下载更多数据，同时也意味着 JavaScript遍历 DOM的效率越慢。比如当你增加一个事件句柄时在 500和 5000个 DOM元素中循环效果肯定是不一样的。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大量的 DOM元素的存在意味着页面中有可以不用移除内容只需要替换元素标签就可以精简的部分。你在页面布局中使用表格了吗？你有没有仅仅为了布局而引入更多的</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">元素呢？也许会存在一个适合或者在语意是更贴切的标签可以供你使用。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;YUI CSS utilities 可以给你的布局带来巨大帮助： grids.css可以帮你实现整体布局， font.css和 reset.css可以帮助你移除浏览器默认格式。它提供了一个重新审视你页面中标签 的机会，比如只有在语意上有意义时才使用 div&gt;-，而不是因为它具有换行效果才使用它。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;DOM 元素数量很容易计算出来，只需要在 Firebug的控制台内输入：</span></h2><pre class="ql-syntax" spellcheck="false">document.getElementsByTagName(\'*\').length\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;那么多少个 DOM元素算是多呢？这可以对照有很好标记使用的类似页面。比如 Yahoo!主页是一个内容非常多的页面，但是它只使用了 700个元素（ HTML标签）。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">8、根据域名划分页面内容</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;把页面内容划分成若干部分可以使你最大限度地实现平行下载。由于 DNS查找带来的影响你首先要确保你使用的域名数量在 2个到 4个之间。例如，你可以把用到的 HTML内容和动态内容放在&nbsp;http://www.example.org/&nbsp;上，而把页面各种组件（图片、脚本、 CSS)分别存放在 statics1.example.org和 statics.example.org上。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;你可在 Tenni Theurer和 Patty Chi合写的文章 Maximizing Parallel Downloads in the Carpool Lane找到更多相关信息。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">9、使 iframe的数量最小</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;ifrmae 元素可以在父文档中插入一个新的 HTML文档。了解 iframe的工作理然后才能更加有效地使用它，这一点很重要。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">iframe优点：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">解决加载缓慢的第三方内容如图标和广告等的加载问题</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Security sandbox</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">并行加载脚本</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">iframe的缺点：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">即时内容为空，加载也需要时间</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">会阻止页面加载</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">没有语意</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">10、不要出现 404错误</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;HTTP 请求时间消耗是很大的，因此使用 HTTP请求来获得一个没有用处的响应（例如 404没有找到页面）是完全没有必要的，它只会降低用户体验而不会有一点好处。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;有些站点把 404错误响应页面改为你是不是要找 ***，这虽然改进了用户体验但是同样也会浪费服务器资源（如数据库等）。最糟糕的 情况是指向外部 JavaScript的链接出现问题并返回 404代码。首先，这种加载会破坏并行加载；其次浏览器会把试图在返回的404响应内容中找到可能有用的部分当作 JavaScript代码来执行。</span></h2><pre class="ql-syntax" spellcheck="false">二、服务器部分\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">使用内容分发网络</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">为文件头指定Expires或Cache-Control</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Gzip压缩文件内容</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">配置ETag</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">尽早刷新输出缓冲</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">使用GET来完成AJAX请求</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">避免空的图像来源</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">11、使用内容分发网络</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;用户与你网站服务器的接近程度会影响响应时间的长短。把你的网站内容分散到多个、处于不同地域位置的服务器上可以加快下载速度。但是首先我们应该做些什么呢？</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;按地域布置网站内容的第一步并不是要尝试重新架构你的网站让他们在分发服务器上正常运行。根据应用的需求来改变网站结构，这可能会包括一些比较复杂的任 务，如在服务器间同步Session状态和合并数据库更新等。要想缩短用户和内容服务器的距离，这些架构步骤可能是不可避免的。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;要记住，在终端用户的响应时间中有80%到90%的响应时间用于下载图像、样式表、脚本、Flash等页面内容。这就是网站性能黄金守则。和重新设计你的 应用程序架构这样比较困难的任务相比，首先来分布静态内容会更好一点。这不仅会缩短响应时间，而且对于内容分发网络来说它更容易实现。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;内容分发网络（Content Delivery Network，CDN）是由一系列分散到各个不同地理位置上的Web服务器组成的，它提高了网站内容的传输速度。用于向用户传输内容的服务器主要是根据 和用户在网络上的靠近程度来指定的。例如，拥有最少网络跳数（network hops）和响应速度最快的服务器会被选定。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;一些大型的网络公司拥有自己的CDN，但是使用像Akamai Technologies，Mirror Image Internet，或者Limelight Networks这样的CDN服务成本却非常高。对于刚刚起步的企业和个人网站来说，可能没有使用CDN的成本预算，但是随着目标用户群的不断扩大和更加 全球化，CDN就是实现快速响应所必需的了。以Yahoo来说，他们转移到CDN上的网站程序静态内容节省了终端用户20%以上的响应时间。使用CDN是一个只需要相对简单地修改代码实现显著改善网站访问速度的方法。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">12、为文件头指定Expires或Cache-Control</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">这条守则包括两方面的内容：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">对于静态内容：设置文件头过期时间Expires的值为“Never expire”（永不过期）</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">对于动态内容：使用恰当的Cache-Control文件头来帮助浏览器进行有条件的请求</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;网页内容设计现在越来越丰富，这就意味着页面中要包含更多的脚本、样式表、图片和Flash。第一次访问你页面的用户就意味着进行多次的HTTP请求，但 是通过使用Expires文件头就可以使这样内容具有缓存性。它避免了接下来的页面访问中不必要的HTTP请求。Expires文件头经常用于图像文件， 但是应该在所有的内容都使用他，包括脚本、样式表和Flash等。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;浏览器（和代理）使用缓存来减少HTTP请求的大小和次数以加快页面访问速度。Web服务器在HTTP响应中使用Expires文件头来告诉客户端内容需 要缓存多长时间。下面这个例子是一个较长时间的Expires文件头，它告诉浏览器这个响应直到2010年4月15日才过期。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Expires: Thu, 15 Apr 2010 20:00:00 GMT</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;如果你使用的是Apache服务器，可以使用ExpiresDefault来设 ExpiresDefault “access plus 10 years”</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;要切记，如果使用了Expires文件头，当页面内容改变时就必须改变内容的文件名。依Yahoo!来说我们经常使用这样的步骤：在内容的文件名中加上版 本号，如yahoo_2.0.6.js。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;使用Expires文件头只有会在用户已经访问过你的网站后才会起作用。当用户首次访问你的网站时这对减少HTTP请求次数来说是无效的，因为浏览器的缓 存是空的。因此这种方法对于你网站性能的改进情况要依据他们“预缓存”存在时对你页面的点击频率（“预缓存”中已经包含了页面中的所有内容）。 Yahoo!建立了一套测量方法，我们发现所有的页面浏览量中有75~85%都有“预缓存”。通过使用Expires文件头，增加了缓存在浏览器中内容的 数量，并且可以在用户接下来的请求中再次使用这些内容，这甚至都不需要通过用户发送一个字节的请求。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">13、Gzip压缩文件内容</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;网络传输中的HTTP请求和应答时间可以通过前端机制得到显著改善。的确，终端用户的带宽、互联网提供者、与对等交换点的靠近程度等都不是网站开发者所能 决定的。但是还有其他因素影响着响应时间。通过减小HTTP响应的大小可以节省HTTP响应时间。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;从HTTP/1.1开始，web客户端都默认支持HTTP请求中有Accept-Encoding文件头的压缩格式：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Accept-Encoding: gzip, deflate</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;如果web服务器在请求的文件头中检测到上面的代码，就会以客户端列出的方式压缩响应内容。Web服务器把压缩方式通过响应文件头中的Content- Encoding来返回给浏览器。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Content-Encoding: gzip</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Gzip是目前最流行也是最有效的压缩方式。这是由GNU项目开发并通过RFC 1952来标准化的。另外仅有的一个压缩格式是deflate，但是它的使用范围有限效果也稍稍逊色。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Gzip大概可以减少70%的响应规模。目前大约有90%通过浏览器传输的互联网交换支持gzip格式。如果你使用的是Apache，gzip模块配置和 你的版本有关：Apache 1.3使用mod_zip，而Apache 2.x使用moflate。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;浏览器和代理都会存在这样的问题：浏览器期望收到的和实际接收到的内容会存在不匹配的现象。幸好，这种特殊情况随着旧式浏览器使用量的减少在减少。 Apache模块会通过自动添加适当的Vary响应文件头来避免这种状况的出现。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;服务器根据文件类型来选择需要进行gzip压缩的文件，但是这过于限制了可压缩的文件。大多数web服务器会压缩HTML文档。对脚本和样式表进行压缩同 样也是值得做的事情，但是很多web服务器都没有这个功能。实际上，压缩任何一个文本类型的响应，包括XML和JSON，都值得的。图像和PDF文件由于 已经压缩过了所以不能再进行gzip压缩。如果试图gizp压缩这些文件的话不但会浪费CPU资源还会增加文件的大小。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Gzip压缩所有可能的文件类型是减少文件体积增加用户体验的简单方法。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">14、配置ETag</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Entity tags（ETags）（实体标签）是web服务器和浏览器用于判断浏览器缓存中的内容和服务器中的原始内容是否匹配的一种机制（“实体”就是所说的“内 容”，包括图片、脚本、样式表等）。增加ETag为实体的验证提供了一个比使用“last-modified date（上次编辑时间）”更加灵活的机制。Etag是一个识别内容版本号的唯一字符串。唯一的格式限制就是它必须包含在双引号内。原始服务器通过含有 ETag文件头的响应指定页面内容的ETag。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">HTTP/1.1 200 OK</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Last-Modified: Tue, 12 Dec 2006 03:03:59 GMT</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">ETag: “10c24bc-4ab-457e1c1f”</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Content-Length: 12195</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;稍后，如果浏览器要验证一个文件，它会使用If-None-Match文件头来把ETag传回给原始服务器。在这个例子中，如果ETag匹配，就会返回一 个304状态码，这就节省了12195字节的响应。 GET /i/yahoo.gif HTTP/1.1</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Host: us.yimg.com</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">If-Modified-Since: Tue, 12 Dec 2006 03:03:59 GMT</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">If-None-Match: “10c24bc-4ab-457e1c1f”</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">HTTP/1.1 304 Not Modified</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;ETag的问题在于，它是根据可以辨别网站所在的服务器的具有唯一性的属性来生成的。当浏览器从一台服务器上获得页面内容后到另外一台服务器上进行验证时 ETag就会不匹配，这种情况对于使用服务器组和处理请求的网站来说是非常常见的。默认情况下，Apache和IIS都会把数据嵌入ETag中，这会显著 减少多服务器间的文件验证冲突。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Apache 1.3和2.x中的ETag格式为inode-size-timestamp。即使某个文件在不同的服务器上会处于相同的目录下，文件大小、权限、时间戳 等都完全相同，但是在不同服务器上他们的内码也是不同的。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;IIS 5.0和IIS 6.0处理ETag的机制相似。IIS中的ETag格式为Filetimestamp:ChangeNumber。用ChangeNumber来跟踪 IIS配置的改变。网站所用的不同IIS服务器间ChangeNumber也不相同。不同的服务器上的Apache和IIS即使对于完全相同的内容产生的 ETag在也不相同，用户并不会接收到一个小而快的304响应；相反他们会接收一个正常的200响应并下载全部内容。如果你的网站只放在一台服务器上，就 不会存在这个问题。但是如果你的网站是架设在多个服务器上，并且使用Apache和 IIS产生默认的ETag配置，你的用户获得页面就会相对慢一点，服务器会传输更多的内容，占用更多的带宽，代理也不会有效地缓存你的网站内容。即使你的 内容拥有Expires文件头，无论用户什么时候点击“刷新”或者“重载”按钮都会发送相应的GET请求。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;如果你没有使用ETag提供的灵活的验证模式，那么干脆把所有的ETag都去掉会更好。Last-Modified文件头验证是基于内容的时间戳的。去掉 ETag文件头会减少响应和下次请求中文件的大小。微软的这篇支持文稿讲述了如何去掉ETag。在Apache中，只需要在配置文件中简单添加下面一行代 码就可以了：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">FileETag none</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">15、尽早刷新输出缓冲</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;当用户请求一个页面时，无论如何都会花费200到500毫秒用于后台组织HTML文件。在这期间，浏览器会一直空闲等待数据返回。在PHP中，你可以使用 flush()方法，它允许你把已经编译的好的部分HTML响应文件先发送给浏览器，这时浏览器就会可以下载文件中的内容（脚本等）而后台同时处理剩余的 HTML页面。这样做的效果会在后台烦恼或者前台较空闲时更加明显。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;输出缓冲应用最好的一个地方就是紧跟在head之后，因为HTML的头部分容易生成而且头部往往包含CSS和JavaScript文件，这样浏览器就可以在后台编译剩余HTML的同时并行下 载它们。 例子：</span></h2><pre class="ql-syntax" spellcheck="false">... &lt;!-- css, js --&gt;\n    &lt;/head&gt;\n    &lt;?php flush(); ?&gt;\n    &lt;body&gt;\n... &lt;!-- content --&gt;\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;为了证明使用这项技术的好处，Yahoo!搜索率先研究并完成了用户测试。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">16、使用GET来完成AJAX请求</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Yahoo!Mail团队发现，当使用XMLHttpRequest时，浏览器中的POST方法是一个“两步走”的过程：首先发送文件头，然后才发送数 据。因此使用GET最为恰当，因为它只需发送一个TCP包（除非你有很多cookie）。IE中URL的最大长度为2K，因此如果你要发送一个超过2K的 数据时就不能使用GET了。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;一个有趣的不同就是POST并不像GET那样实际发送数据。根据HTTP规范，GET意味着“获取”数据，因此当你仅仅获取数据时使用GET更加有意义 （从语意上讲也是如此），相反，发送并在服务端保存数据时使用POST。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;除此之外，JavaScript和CSS也是我们页面中经常用到的内容，对它们的优化也提高网站性能的重要方面。</span></h2><pre class="ql-syntax" spellcheck="false">三、CSS部分\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">把样式表置于顶部</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">避免使用CSS表达式（）</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">用代替@import</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">避免使用滤镜</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">17、把样式表置于顶部</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在研究Yahoo!的性能表现时，我们发现把样式表放到文档的head内部似乎会加快页面的下载速度。这是因为把样式表放到head内会使页面有步骤的加载显示。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注重性能的前端服务器往往希望页面有秩序地加载。同时，我们也希望浏览器把已经接收到内容尽可能显示出来。这对于拥有较多内容的页面和网速较慢的用户来说 特别重要。向用户返回可视化的反馈，比如进程指针，已经有了较好的研究并形成了正式文档。在我们的研究中HTML页面就是进程指针。当浏览器有序地加载文 件头、导航栏、顶部的logo等对于等待页面加载的用户来说都可以作为可视化的反馈。这从整体上改善了用户体验。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;把样式表放在文档底部的问题是在包括Internet Explorer在内的很多浏览器中这会中止内容的有序呈现。浏览器中止呈现是为了避免样式改变引起的页面元素重绘。用户不得不面对一个空白页面。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;HTML规范清楚指出样式表要放包含在页面的head区域内：“和a不同，link只能出现在文档的head区域内，尽管它可以多次使用它”。无论是引起白屏还是出现没有样式化的内容都不值得去尝试。最好的方案就是按照HTML规范在文 档head内加载你的样式表。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">18、避免使用CSS表达式（）</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;CSS表达式是动态设置CSS属性的强大（但危险）方法。Internet Explorer从第5个版本开始支持CSS表达式。下面的例子中，使用CSS表达式可以实现隔一个小时切换一次背景颜色：</span></h2><pre class="ql-syntax" spellcheck="false">background-color: ( (new Date()).getHours()%2 ? "#B8D4FF" : "#F08A00" );\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;如上所示，中使用了JavaScript表达式。CSS属性根据JavaScript表达式的计算结果来设置。 方法在其它浏览器中不起作用，因此在跨浏览器的设计中单独针对Internet Explorer设置时会比较有用。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;表达式的问题就在于它的计算频率要比我们想象的多。不仅仅是在页面显示和缩放时，就是在页面滚动、乃至移动鼠标时都会要重新计算一次。给CSS表达式增加 一个计数器可以跟踪表达式的计算频率。在页面中随便移动鼠标都可以轻松达到10000次以上的计算量。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;一个减少CSS表达式计算次数的方法就是使用一次性的表达式，它在第一次运行时将结果赋给指定的样式属性，并用这个属性来代替CSS表达式。如果样式属性 必须在页面周期内动态地改变，使用事件句柄来代替CSS表达式是一个可行办法。如果必须使用CSS表达式，一定要记住它们要计算成千上万次并且可能会对你 页面的性能产生影响。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">19、用代替@import</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;前面的最佳实现中提到CSS应该放置在顶端以利于有序加载呈现。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在IE中，页面底部@import和使用link作用是一样的，因此最好不要使用它。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">20、避免使用滤镜</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;IE独有属性AlphaImageLoader用于修正7.0以下版本中显示PNG图片的半透明效果。这个滤镜的问题在于浏览器加载图片时它会终止内容的 呈现并且冻结浏览器。在每一个元素（不仅仅是图片）它都会运算一次，增加了内存开支，因此它的问题是多方面的。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;完全避免使用AlphaImageLoader的最好方法就是使用PNG8格式来代替，这种格式能在IE中很好地工作。如果你确实需要使用 AlphaImageLoader，请使用下划线_filter又使之对IE7以上版本的用户无效。</span></h2><pre class="ql-syntax" spellcheck="false">四、 JavaScript部分\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">把脚本置于页面底部</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">使用外部JavaScript和CSS</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">削减JavaScript和CSS</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">剔除重复脚本</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">减少DOM访问</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">发智能事件处理程序</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">21、把脚本置于页面底部</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;脚本带来的问题就是它阻止了页面的平行下载。HTTP/1.1 规范建议，浏览器每个主机名的并行下载内容不超过两个。如果你的图片放在多个主机名上，你可以在每个并行下载中同时下载2个以上的文件。但是当下载脚本 时，浏览器就不会同时下载其它文件了，即便是主机名不相同。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在某些情况下把脚本移到页面底部可能不太容易。比如说，如果脚本中使用了document.write来插入页面内容，它就不能被往下移动了。这里可能还 会有作用域的问题。很多情况下，都会遇到这方面的问题。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;一个经常用到的替代方法就是使用延迟脚本。DEFER属性表明脚本中没有包含document.write，它告诉浏览器继续显示。不幸的 是，Firefox并不支持DEFER属性。在Internet Explorer中，脚本可能会被延迟但效果也不会像我们所期望的那样。如果脚本可以被延迟，那么它就可以移到页面的底部。这会让你的页面加载的快一点。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">22、使用外部JavaScript和CSS</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;很多性能规则都是关于如何处理外部文件的。但是，在你采取这些措施前你可能会问到一个更基本的问题：JavaScript和CSS是应该放在外部文件中呢 还是把它们放在页面本身之内呢？</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在实际应用中使用外部文件可以提高页面速度，因为JavaScript和CSS文件都能在浏览器中产生缓存。内置在HTML文档中的JavaScript 和CSS则会在每次请求中随HTML文档重新下载。这虽然减少了HTTP请求的次数，却增加了HTML文档的大小。从另一方面来说，如果外部文件中的 JavaScript和CSS被浏览器缓存，在没有增加HTTP请求次数的同时可以减少HTML文档的大小。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;关键问题是，外部JavaScript和CSS文件缓存的频率和请求HTML文档的次数有关。虽然有一定的难度，但是仍然有一些指标可以一测量它。如果一 个会话中用户会浏览你网站中的多个页面，并且这些页面中会重复使用相同的脚本和样式表，缓存外部文件就会带来更大的益处。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;许多网站没有功能建立这些指标。对于这些网站来说，最好的坚决方法就是把JavaScript和CSS作为外部文件引用。比较适合使用内置代码的例外就是 网站的主页，如Yahoo!主页和My Yahoo!。主页在一次会话中拥有较少（可能只有一次）的浏览量，你可以发现内置JavaScript和CSS对于终端用户来说会加快响应时 间。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;对于拥有较大浏览量的首页来说，有一种技术可以平衡内置代码带来的HTTP请求减少与通过使用外部文件进行缓存带来的好处。其中一个就是在首页中内置 JavaScript和CSS，但是在页面下载完成后动态下载外部文件，在子页面中使用到这些文件时，它们已经缓存到浏览器了。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">23、削减JavaScript和CSS</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;精简是指从去除代码不必要的字符减少文件大小从而节省下载时间。消减代码时，所有的注释、不需要的空白字符（空格、换行、tab缩进）等都要去掉。在 JavaScript中，由于需要下载的文件体积变小了从而节省了响应时间。精简JavaScript中目前用到的最广泛的两个工具是JSMin和YUI Compressor。YUI Compressor还可用于精简CSS。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;混淆是另外一种可用于源代码优化的方法。这种方法要比精简复杂一些并且在混淆的过程更易产生问题。在对美国前10大网站的调查中发现，精简也可以缩小原来 代码体积的21%，而混淆可以达到25%。尽管混淆法可以更好地缩减代码，但是对于JavaScript来说精简的风险更小。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;除消减外部的脚本和样式表文件外，script和style代码块也可以并且应该进行消减。即使你用Gzip压缩过脚本 和样式表，精简这些文件仍然可以节省5%以上的空间。由于JavaScript和CSS的功能和体积的增加，消减代码将会获得益处。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">24、剔除重复脚本</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在同一个页面中重复引用JavaScript文件会影响页面的性能。你可能会认为这种情况并不多见。对于美国前10大网站的调查显示其中有两家存在重复引 用脚本的情况。有两种主要因素导致一个脚本被重复引用的奇怪现象发生：团队规模和脚本数量。如果真的存在这种情况，重复脚本会引起不必要的HTTP请求和 无用的JavaScript运算，这降低了网站性能。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在Internet Explorer中会产生不必要的HTTP请求，而在Firefox却不会。在Internet Explorer中，如果一个脚本被引用两次而且它又不可缓存，它就会在页面加载过程中产生两次HTTP请求。即时脚本可以缓存，当用户重载页面时也会产 生额外的HTTP请求。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;除增加额外的HTTP请求外，多次运算脚本也会浪费时间。在Internet Explorer和Firefox中不管脚本是否可缓存，它们都存在重复运算JavaScript的问题。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;一个避免偶尔发生的两次引用同一脚本的方法是在模板中使用脚本管理模块引用脚本。在HTML页面中使用script标签引用脚本的最常见方法就是：</span></h2><pre class="ql-syntax" spellcheck="false">&lt;script type="text/javascript" src="menu_1.0.17.js"&gt;&lt;/script&gt;\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在PHP中可以通过创建名为insertScript的方法来替代：</span></h2><pre class="ql-syntax" spellcheck="false">&lt;?php insertScript("menu.js") ?&gt;\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;为了防止多次重复引用脚本，这个方法中还应该使用其它机制来处理脚本，如检查所属目录和为脚本文件名中增加版本号以用于Expire文件头等。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">25、减少DOM访问</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;使用JavaScript访问DOM元素比较慢，因此为了获得更多的应该页面，应该做到：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">缓存已经访问过的有关元素</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">线下更新完节点之后再将它们添加到文档树中</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">避免使用JavaScript来修改页面布局</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;有关此方面的更多信息请查看Julien Lecomte在YUI专题中的文章“高性能Ajax应该程序”。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">26、开发智能事件处理程序</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;有时候我们会感觉到页面反应迟钝，这是因为DOM树元素中附加了过多的事件句柄并且些事件句病被频繁地触发。这就是为什么说使用event delegation（事件代理）是一种好方法了。如果你在一个div中有10个按钮，你只需要在div上附加一次事件句柄就可以了，而不用去为每一个按 钮增加一个句柄。事件冒泡时你可以捕捉到事件并判断出是哪个事件发出的。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;你同样也不用为了操作DOM树而等待onload事件的发生。你需要做的就是等待树结构中你要访问的元素出现。你也不用等待所有图像都加载完毕。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;你可能会希望用DOMContentLoaded事件来代替 事件应用程序中的onAvailable方法。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;有关此方面的更多信息请查看Julien Lecomte在YUI专题中的文章“高性能Ajax应该程序”。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;图片和Coockie也是我们网站中几乎不可缺少组成部分，此外随着移动设备的流行，对于移动应用的优化也十分重要。这主要包括：</span></h2><pre class="ql-syntax" spellcheck="false">五、Coockie部分\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">减小Cookie体积</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">对于页面内容使用无coockie域名</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">27、减小Cookie体积</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;HTTP coockie可以用于权限验证和个性化身份等多种用途。coockie内的有关信息是通过HTTP文件头来在web服务器和浏览器之间进行交流的。因此 保持coockie尽可能的小以减少用户的响应时间十分重要。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">有关更多信息可以查看Tenni Theurer和Patty Chi的文章“When the Cookie Crumbles”。这们研究中主要包括：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">去除不必要的coockie</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">使coockie体积尽量小以减少对用户响应的影响</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">注意在适应级别的域名上设置coockie以便使子域名不受影响</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">设置合理的过期时间。较早地Expire时间和不要过早去清除coockie，都会改善用户的响应时间。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">28、对于页面内容使用无coockie域名</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;当浏览器在请求中同时请求一张静态的图片和发送coockie时，服务器对于这些coockie不会做任何地使用。因此他们只是因为某些负面因素而创建的 网络传输。所有你应该确定对于静态内容的请求是无coockie的请求。创建一个子域名并用他来存放所有静态内容。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;如果你的域名是http://www.example.org/，你可以在static.example.org上存在静态内容。但是，如果你不是在http://www.example.org/&nbsp;上而是在顶级域名example.org设置了coockie，那么所有对于static.example.org的请求都包含coockie。在这种情 况下，你可以再重新购买一个新的域名来存在静态内容，并且要保持这个域名是无coockie的。Yahoo!使用的是ymig.com，YouTube使 用的是ytimg.com，Amazon使用的是images-anazon.com等等。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;使用无coockie域名存在静态内容的另外一个好处就是一些代理（服务器）可能会拒绝对coockie的内容请求进行缓存。一个相关的建议就是，如果你 想确定应该使用example.org还是http://www.example.org/&nbsp;作为你的一主页，你要考虑到coockie带来的影响。忽略掉www会使你除了把coockie设置到*.example.org（*是泛域名解析，代表 了所有子域名译者dudo注）外没有其它选择，因此出于性能方面的考虑最好是使用带有www的子域名并且在它上面设置coockie。</span></h2><pre class="ql-syntax" spellcheck="false">六、Image 部分\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">优化图像</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">优化CSS Spirite</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">不要在HTML中缩放图像</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">favicon.ico要小而且可缓存</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">29、优化图像</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;设计人员完成对页面的设计之后，不要急于将它们上传到web服务器，这里还需要做几件事：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">你可以检查一下你的GIF图片中图像颜色的数量是否和调色板规格一致。 使用imagemagick中下面的命令行很容易检查：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">identify -verbose image.gif</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;如果你发现图片中只用到了4种颜色，而在调色板的中显示的256色的颜色槽，那么这张图片就还有压缩的空间。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;尝试把GIF格式转换成PNG格式，看看是否节省空间。大多数情况下是可以压缩的。由于浏览器支持有限，设计者们往往不太乐意使用PNG格式的图片，不过 这都是过去的事情了。现在只有一个问题就是在真彩PNG格式中的alpha通道半透明问题，不过同样的，GIF也不是真彩格式也不支持半透明。因此GIF 能做到的，PNG（PNG8）同样也能做到（除了动画）。下面这条简单的命令可以 安全地把GIF格式转换为PNG格式：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">convert image.gif image.png</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">“我们要说的是：给PNG一个施展身手的机会吧！”</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">在所有的PNG图片上运行pngcrush（或者其它PNG优化工具）。例如：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">pngcrush image.png -rem alla -reduce -brute result.png</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">在所有的JPEG图片上运行jpegtran。这个工具可以对图片中的出现的锯齿等做无损操作，同时它还可以用于优化和清除图片中的注释以及其它无用信息 （如EXIF信息）：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">jpegtran -copy none -optimize -perfect src.jpg dest.jpg</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">30、优化CSS Spirite</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在Spirite中水平排列你的图片，垂直排列会稍稍增加文件大小；</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Spirite中把颜色较近的组合在一起可以降低颜色数，理想状况是低于256色以便适用PNG8格式；</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;便于移动，不要在Spirite的图像中间留有较大空隙。这虽然不大会增加文件大小但对于用户代理来说它需要更少的内存来把图片解压为像素地图。 100x100的图片为1万像素，而1000x1000就是100万像素。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">31、不要在HTML中缩放图像</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;不要为了在HTML中设置长宽而使用比实际需要大的图片。如果你需要：</span></h2><pre class="ql-syntax" spellcheck="false">&lt;img width="100" height="100" src="mycat.jpg" alt="My Cat" /&gt;\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;那么你的图片（mycat.jpg）就应该是100x100像素而不是把一个500x500像素的图片缩小使用。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">32、favicon.ico要小而且可缓存</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;favicon.ico是位于服务器根目录下的一个图片文件。它是必定存在的，因为即使你不关心它是否有用，浏览器也会对它发出请求，因此最好不要返回一 个404 Not Found的响应。由于是在同一台服务器上，它每被请求一次coockie就会被发送一次。这个图片文件还会影响下载顺序，例如在IE中当你在 onload中请求额外的文件时，favicon会在这些额外内容被加载前下载。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;因此，为了减少favicon.ico带来的弊端，要做到：</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">文件尽量地小，最好小于1K</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">在适当的时候（也就是你不要打算再换favicon.ico的时候，因为更换新文件时不能对它进行重命名）为它设置Expires文件头。你可以很安全地 把Expires文件头设置为未来的几个月。你可以通过核对当前favicon.ico的上次编辑时间来作出判断。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Imagemagick可以帮你创建小巧的favicon。</span></h2><pre class="ql-syntax" spellcheck="false">七、 Mobile部分\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">保持单个内容小于25K</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">打包组件成复合文本</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">33、保持单个内容小于25K</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;这条限制主要是因为iPhone不能缓存大于25K的文件。注意这里指的是解压缩后的大小。由于单纯gizp压缩可能达不要求，因此精简文件就显得十分重要。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">34、打包组件成复合文本</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;把页面内容打包成复合文本就如同带有多附件的Email，它能够使你在一个HTTP请求中取得多个组件（切记：HTTP请求是很奢侈的）。当你使用这条规 则时，首先要确定用户代理是否支持（iPhone就不支持）。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">35、避免空的图像来源</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">一个src属性为空串的图像有两种情况：</span></h2><pre class="ql-syntax" spellcheck="false">1． 直接的HTML\n&lt;img src=""&gt;\n2． JavaScript\nvar img = new Image();\nimg.src = "";\n</pre><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;这两种情况都会引起同样的效果：浏览器会再次向你的服务器发出请求。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Internet Explorer 将向这个页面所在的目录发出一个请求</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Safari and Chrome 将发出对这个页面的一个请求。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Firefox 3 和更早的版本所采取的动作和Safari and Chrome一样，但是 3.5版本 addressed this issue[bug 444931] and no longer sends a request.</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">Opera 不进行任何操作。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;这个行为为何是不好的？</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">发送大量突然的请求将使你的服务器宕机（Cripple your servers），尤其是每天有数百万访问量的页面。</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">产生一个从未浏览过的页面将浪费服务器的计算周期（computing cycles）</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">损坏用户数据。如果你在请求中追踪状态（以cookie或是其他的方式），你可能会损坏数据。即使这个图像请求并没有返回一个图像，所有的头被浏览器读取并接受，包括所有cookie。While the rest of the response is thrown away, the damage may already be done.</span></h2><h2><span class="ql-size-14px" style="color: rgb(68, 68, 68);">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;引起这种行为的根源在于浏览器中URI的解析方式。这种行为定义在RFC 3986 - Uniform Resource Identifiers.当一个空串作为一个URI时，它被认为一个相对URI（relative URI）并通过定义在section 5.2中的算法被解析。这个特例，一个空串，列在section 5.4当中。Firefox, Safari, and Chrome都是依据这一规格来解析空串，而Internet Explorer则不正确的解析这个串，符合更早的一个规范，RFC 2396 - Uniform Resource Identifiers (this was obsoleted by RFC 3986).所以技术上，浏览器都在做它们被期望所做的事情来解析relative URIs，问题是在这个范围，空串不是故意造成的。</span></h2>',
          __v: 0
        }
      ],
      carouselList: [],
      classes: [
        {
          _id: "5c947642779de5468066c488",
          classesName: "前端",
          classesDesc: "关于前端的一些知识点",
          __v: 0
        },
        {
          _id: "5c94abae4fc7fd0248421414",
          classesName: "生活",
          classesDesc: "记录生活的点滴",
          __v: 0
        },
        {
          _id: "5c999007af11fc697c866937",
          classesName: "面试",
          classesDesc: "一些面试技巧及经验",
          __v: 0
        }
      ],
      carouselShow: true,
      articleList: false,
      total: 0,
      loading: true
    };
  },
  beforeCreate() {},
  created() {
    this.carouselList = this.articleLists.slice(0, 5);
    this.loading = false;
  },
  methods: {
    toDetail(id) {
      this.$router.push({ name: "articledetail", params: { id: id } });
    }
  }
};
</script>
<style scoped>
.content_box {
  display: flex;
  flex-wrap: wrap;
}
.content_box a {
  cursor: pointer;
}

.wx_code {
  width: 100%;
}
.block__title {
  position: relative;
  color: #242f35;
  font-size: 16px;
  padding-left: 16px;
  margin: 0px auto 20px;
  font-weight: 400;
  height: 20px;
  line-height: 20px;
}
.block__title:before {
  content: "";
  display: block;
  position: absolute;
  width: 4px;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background: #409eff !important;
  background-size: 100%;
}
.item__cover {
  position: relative;
  display: block;
  width: 80px;
  height: 60px;
  overflow: hidden;
}
.item__cover > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.latest-post-item > a {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  width: 100%;
  text-decoration: none;
  color: #242f35;
}
.latest-post-item .item__info {
  display: block;
  width: calc(100% - 110px);
  padding: 8px 15px;
  line-height: 1.4;
}
.latest-post-item .item__title {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: color 0.3s;
}
.latest-post-item .item__text {
  font-size: 12px;
  color: #666;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.latest-post-item + .latest-post-item {
  margin-top: 10px;
}
.article_classes {
  font-size: 16px;
  display: inline-block;
  padding: 0 20px;
  font-weight: normal;
  color: #fff;
  margin: 0;
  cursor: pointer;
  height: 30px;
  line-height: 30px;
  background: #409eff;
  border-radius: 20px;
  box-shadow: 0 0 10px #409eff;
}
</style>



