<template>
  <div>
    <h4>找到匹配<span class="keyword_text">{{keyword}}</span>的结果共<span class="keyword_text">{{searchLength}}</span>条</h4>
    <ul class="article_box">
      <li
        @click="toDetail(article._id)"
        v-for="(article,index) in searchLists"
        :key="index"
      >
        <div class="mini-article__cover">
          <img
            itemprop="image"
            :src="article.articleImgurl"
            :alt="article.articleName"
            @error="imgError(article)"
          >
          <div
            itemprop="datePublished"
            content
            class="mini-article__date"
          >
            <span class="date__day">{{article.createTime}}</span>
          </div>
        </div>
        <div class="mini-article__info">
          <h3
            itemprop="name"
            class="mini-article__title"
          >
            <a :title="article.articleName">{{article.articleName}}</a>
          </h3>
          <a :title="article.articleDesc">
            <p
              itemprop="articleSection"
              class="min-article__desc"
            >{{article.articleDesc}}</p>
            <p class="w-Read">
              <span>阅读全文&gt;</span>
            </p>
          </a>
          <div class="min-article__tags">
            <i class="el-icon-bell"></i>
            <ul class="tags__list clearfix">
              <li
                class="tags__item"
                v-for="(tag,index) in article.tags"
                :key="index"
              >
                <a href="javascript:;">{{tag}}</a>
              </li>
            </ul>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import url from "@/api/api.config.js";
export default {
  data() {
    return {
      keyword: "",
      searchLists: [ {
          tags: ["幸运", "童年", "治愈"],
          clickTimes: 0,
          createTime: "2019-03-28T02:09:23.687Z",
          updateTime: "2019-03-28T06:04:10.382Z",
          _id: "5c9c53d7487f0c5b50f63969",
          articleName: "幸与不幸之间",
          articleDesc:
            "幸运之人，一辈子都在用童年治愈，不幸之人，一辈子都在治愈童年",
          articleImgurl: "./static/public/uploads/1553748582756.jpg",
          classesId: {
            _id: "5c94abae4fc7fd0248421414",
            classesName: "生活",
            classesDesc: "记录生活的点滴",
            __v: 0
          },
          articleContent:
            '<h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">当你有信仰的时候，你就年轻；</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">当你疑惑的时候，你就老了；</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">当你有自信的时候，你就年轻；</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">当你有畏惧的时候，你就老了；</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">当你有希望的时候，你就年轻；</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">当你绝望的时候，你就老了；</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">失去热忱的时候，你的灵魂就会长上皱纹。</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px"><img src="http://t11.baidu.com/it/u=813055948,588356076&amp;fm=173&amp;app=25&amp;f=JPEG?w=640&amp;h=459&amp;s=4C02D71C4C1076C4549C9544030070F9"></span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">看，年轻就是这么简单，只要你想，青春一直都在。幸福也是如此。</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">我们经常把自己的不幸福归纳总结，得出各样各样原因。其实有时候想想，幸与不幸，只在一念之间。在于每一个人对待生活的态度。</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px"> 什么是幸福呢？可能每一个人都有自己不同的理解。就算是问同一个人，在不同的时候，对幸福的理解也是不一样的。正如此刻，趁着孩子熟睡，</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">坐在电脑面前敲下几行字的我，虽然屋子里一片狼藉，风扇拼命的摇着也驱不散闷热的感觉，可是，能在此刻，静静的敲下几个字，却觉得是一种莫大的幸福。</span></h1><h1 class="ql-align-center"><br></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px"><img src="http://t11.baidu.com/it/u=1116096193,527837618&amp;fm=173&amp;app=25&amp;f=JPEG?w=640&amp;h=640&amp;s=A9BA6B96436AD6EC1C79B67003001075"></span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">幸福其实很简单，只要你自己认为满足了，那么幸福就悄悄地来到了你的身边。 知足常乐是一种幸福 ，甘于平淡也是一种幸福，就看我们用什么样的心态去看待幸福，</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">去感受幸福。有的时候 ，幸福无处可寻又无处不在 ，生活的点点滴滴都蕴育着幸福 ，有一个温馨的家固然是幸福 ，那么谁又能说一个人的自由就不是幸福呢？</span></h1><h1 class="ql-align-center"><span style="color: rgb(136, 136, 136);" class="ql-size-14px">享受生命的每一个时刻，苦也好，累也罢，只在你乐在其中，又何尝不是一种幸福呢？</span></h1>',
          __v: 0
        },
        {
          tags: ["vue", "全局"],
          clickTimes: 0,
          createTime: "2019-03-26T03:02:20.346Z",
          updateTime: "2019-03-28T04:56:44.013Z",
          _id: "5c9b38b37d28793bfcacdf9d",
          articleName: "在vue项目中 如何定义全局变量 全局函数",
          articleDesc:
            "在项目中，经常有些函数和变量是需要复用，比如说网站服务器地址，从后台拿到的：用户的登录token,用户的地址信息等，这时候就需要设置一波全局变量和全局函数",
          articleImgurl: "./static/public/uploads/1553749000585.jpg",
          classesId: {
            _id: "5c947642779de5468066c488",
            classesName: "前端",
            classesDesc: "关于前端的一些知识点",
            __v: 0
          },
          articleContent:
            '<h2>定义全局变量</h2><h3>原理：</h3><p>设置一个专用的的全局变量模块文件，模块里面定义一些变量初始状态，用export default 暴露出去，在main.js里面使用Vue.prototype挂载到vue实例上面或者在其它地方需要使用时，引入该模块便可。</p><h3>全局变量模块文件：</h3><p>Global.vue文件：</p><pre class="ql-syntax" spellcheck="false">&lt;script&gt;\nconst serverSrc=\'www.baidu.com\';\nconst token=\'12345678\';\nconst hasEnter=false;\nconst userSite="中国钓鱼岛";\n  export default\n  {\n    userSite,//用户地址\n    token,//用户token身份\n    serverSrc,//服务器地址\n    hasEnter,//用户登录状态\n  }\n&lt;/script&gt;复制代码\n</pre><h3>使用方式1：</h3><p><strong>在需要的地方引用进全局变量模块文件</strong>，然后通过文件里面的变量名字获取全局变量参数值。</p><p><strong>在text1.vue组件中使用：</strong></p><pre class="ql-syntax" spellcheck="false">&lt;template&gt;\n    &lt;div&gt;{{ token }}&lt;/div&gt;\n&lt;/template&gt;\n\n&lt;script&gt;\nimport global_ from \'../../components/Global\'//引用模块进来\nexport default {\n name: \'text\',\ndata () {\n    return {\n         token:global_.token,//将全局变量赋值到data里面，也可以直接使用global_.token\n        }\n    }\n}\n&lt;/script&gt;\n&lt;style lang="scss" scoped&gt;\n\n&lt;/style&gt;复制代码\n</pre><h3>使用方式2：</h3><p>在程序入口的main.js文件里面，将上面那个Global.vue文件挂载到Vue.prototype。</p><pre class="ql-syntax" spellcheck="false">    import global_ from \'./components/Global\'//引用文件\n    Vue.prototype.GLOBAL = global_//挂载到Vue实例上面复制代码\n</pre><p>接着在整个项目中不需要再通过引用Global.vue模块文件，直接通过this就可以直接访问Global文件里面定义的全局变量。</p><p>text2.vue：</p><pre class="ql-syntax" spellcheck="false">&lt;template&gt;\n    &lt;div&gt;{{ token }}&lt;/div&gt;\n&lt;/template&gt;\n\n&lt;script&gt;\nexport default {\n name: \'text\',\ndata () {\n    return {\n         token:this.GLOBAL.token,//直接通过this访问全局变量。\n        }\n    }\n}\n&lt;/script&gt;\n&lt;style lang="scss" scoped&gt;\n&lt;/style&gt;复制代码\n</pre><h3>Vuex也可以设置全局变量：</h3><p>通过vuex来存放全局变量，这里东西比较多，也相对复杂一些，有兴趣的小伙伴们，可自行查阅资料，折腾一波、</p><h2>定义全局函数</h2><h3>原理</h3><p>新建一个模块文件，然后在main.js里面通过Vue.prototype将函数挂载到Vue实例上面，通过this.函数名，来运行函数。</p><h3>1. 在main.js里面直接写函数</h3><p>简单的函数可以直接在main.js里面直接写</p><pre class="ql-syntax" spellcheck="false">Vue.prototype.changeData = function (){//changeData是函数名\n  alert(\'执行成功\');\n}复制代码\n</pre><p>组件中调用：</p><pre class="ql-syntax" spellcheck="false">this.changeData();//直接通过this运行函数复制代码\n</pre><h3>2. 写一个模块文件，挂载到main.js上面。</h3><p>base.js文件，文件位置可以放在跟main.js同一级，方便引用</p><pre class="ql-syntax" spellcheck="false">exports.install = function (Vue, options) {\n   Vue.prototype.text1 = function (){//全局函数1\n    alert(\'执行成功1\');\n    };\n    Vue.prototype.text2 = function (){//全局函数2\n    alert(\'执行成功2\');\n    };\n};复制代码\n</pre><p>main.js入口文件：</p><pre class="ql-syntax" spellcheck="false">    import base from \'./base\'//引用\n    Vue.use(base);//将全局函数当做插件来进行注册复制代码\n</pre><p>组件里面调用：</p><pre class="ql-syntax" spellcheck="false">    this.text1();\n    this.text2();\n</pre><p><br></p><p>链接：https://juejin.im/post/59eddbfe6fb9a0450908abb4</p>',
          __v: 0
        }],
      searchLength: 0
    };
  },
  created() {
  },
  methods: {
    toDetail(id) {
      this.$router.push({ name: "articledetail", params: { id: id } });
    }
  }
};
</script>

<style scoped>
.keyword_text {
  color: #409eff;
}
</style>




